  background-color: #ffffff26;
  border-radius: 150% 90% 130% 100%;
  width: 100%;
  height: 100%;
}

.blob img {
  width: 100%;
  max-width: 80%;
  height: auto;
  max-height: 100%;
  margin-top: 10%;
  margin-left: 10%;
}

@media screen and (width <= 768px) {
  .blob {
    max-width: 400px;
    max-height: 400px;
    margin-top: 50px;
  }

  .blob img {
    max-width: 70%;
    max-height: 70%;
    margin-top: 15%;
    margin-left: 15%;
  }
}

.slide-sb {
  animation: 1s ease-in-out forwards fadeOut;
  display: none;
}

.active-sb {
  animation: 1s ease-in-out forwards fadeIn;
  display: block;
}

@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translateX(0);
  }

  to {
    opacity: 0;
    transform: translateX(-20px);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

